@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&family=Poppins:wght@400;500;600;700&display=swap);
.jabo-button{padding:0.5rem 1rem;font-size:1rem;font-family:'Poppins', 'Roboto', 'Helvetica';background:#E0E0E0;font-weight:500;border:none;color:#3F3F3F;box-shadow:0px 2px 3px rgba(51,51,51,0.2);outline:none;cursor:pointer;border-radius:6px;margin:0.25rem;display:flex;align-items:center}.jabo-button span{font-size:1.2rem}.jabo-button span.start_icon{margin-right:0.5rem}.jabo-button span.end_icon{margin-left:0.5rem}.jabo-button:hover{background:#c7c7c7}.jabo-button.disableShadow{box-shadow:none;color:white;background:#3D5AFE}.jabo-button.disableShadow:hover{background:#0a2ffe}.jabo-button.outline{background:transparent;border:1px solid #3D5AFE;color:#3D5AFE;box-shadow:none}.jabo-button.outline:hover{background:rgba(41,98,255,0.1)}.jabo-button.text{background:transparent;border:none;color:#3D5AFE;box-shadow:none}.jabo-button.text:hover{background:rgba(41,98,255,0.1)}.jabo-button.text.disabled:hover{background:transparent}.jabo-button.sm{padding:6px 12px}.jabo-button.md{padding:0.5rem 1rem}.jabo-button.lg{padding:11px 22px}.jabo-button.disabled{color:#9E9E9E}.jabo-button.disabled:hover{background:#E0E0E0}.jabo-button.color-default{background:#E0E0E0}.jabo-button.color-default:hover{background:#c7c7c7}.jabo-button.color-primary{color:white;background:#3D5AFE}.jabo-button.color-primary:hover{background:#0a2ffe}.jabo-button.color-secondary{color:white;background:#455A64}.jabo-button.color-secondary:hover{background:#303f46}.jabo-button.color-danger{color:white;background:#D32F2F}.jabo-button.color-danger:hover{background:#ab2424}

*{margin:0px;padding:0px;box-sizing:border-box}.grid-2{display:grid;grid-template-columns:1fr 1fr}.grid-3{display:grid;grid-template-columns:repeat(3, 1fr)}.grid-4{display:grid;grid-template-columns:repeat(4, 1fr)}.group-button{padding:1rem;margin:0.25rem;border:1px solid rgba(41,98,255,0.1);border-radius:8px;background:rgba(41,98,255,0.1)}.group-button p{color:#121212;font-family:monospace;font-weight:bold}.footer{background:#121212;padding:1rem;margin-top:2.5rem}.footer h3{font-family:'Poppins';color:white;text-shadow:0px 2px 3px rgba(51,51,51,0.2);text-align:center;font-size:0.7rem;font-weight:normal}

